}

.navigation__wrap .navigation_list .navigation_item .navigation_link {
  color: #000;
  transform-origin: 50% 0px 0px;
  transform-style: preserve-3d;
  padding: 1rem;
  text-decoration: none;
  transition: transform .3s;
  display: inline-block;
}

.navigation__wrap .navigation_list .navigation_item .navigation_link.current {
