}

.navigation__wrap .navigation_list .navigation_item .navigation_link:hover {
  background: var(--maim-bg-color-and-border);
  color: #000;
  transform: rotateX(90deg)translateY(-22px);
}

.navigation__wrap .navigation_list .navigation_item .navigation_link:before {
  text-align: center;
  background: none repeat scroll 0% 0% var(--maim-bg-color-and-border);
  color: #fff;
  content: attr(data-hover);
  transition: #6363ce .3s ease 0s;
  transform-origin: 50% 0px 0px;
  width: 100%;
  padding: 4px 0;
  line-height: 42px;
  position: absolute;
  top: 100%;
  left: 0;
  transform: rotateX(-90deg);
