:root {
  --bgcolor: #262529;
  --clockheight: 220px;
  --radius: 30px;
  --rotationtime: .55s;
  --avobg: #e3f668;
}

.action-description, .action-title {
  text-align: center;
  margin-bottom: 40px;
  font-size: 40px;
}

.action-wraper {
  height: calc(var(--clockheight)  + 150px);
  background-color: var(--bgcolor);
  color: #fff;
  justify-content: center;
  align-items: center;
  margin: 0;
  display: flex;
}

.clock {
  grid-column-gap: 8px;
  height: var(--clockheight);
  border-radius: var(--radius);
  background-image: linear-gradient(#0e0e0f 0%, #1a191c 20%, #2c2c34 50%, #14141b 100%);
  grid-template-columns: 1fr 1fr 1fr 1fr;
  min-width: 100px;
  display: grid;
  box-shadow: inset 0 -3px 6px 3px rgba(0, 0, 0, .2), inset 0 4px 8px 3px rgba(0, 0, 0, .4), 0 2px 3px 1px rgba(255, 255, 255, .3), 0 -2px 4px 4px rgba(56, 56, 61, .5);
}

@media screen and (min-width: 768px) {
  .clock {
    grid-column-gap: 10px;
    min-width: 150px;
    padding: 0 12px;
  }
}

@media screen and (min-width: 1200px) {
  .clock {
    grid-column-gap: 12px;
    min-width: 200px;
  }
}

.flipper {
  transform-style: preserve-3d;
  perspective: 1600px;
  width: 100%;
  min-width: 100px;
  height: 100%;
  position: relative;
}

@media screen and (min-width: 768px) {
  .flipper {
    min-width: 180px;
  }
}

@media screen and (min-width: 1200px) {
  .flipper {
    min-width: 250px;
  }
}

.gear {
  top: calc(var(--clockheight) / 3);
  height: calc(var(--clockheight) / 3);
  z-index: 99;
  -webkit-transform-style: preserve-3d;
  -webkit-perspective: 0;
  background: linear-gradient(#000 0%, #666 17%, #7f7f7f 52%, #7f7f7f 52%, #0c0c0c 53%, #595959 87%, #131313 100%);
  outline: 3px solid #000;
  width: 8px;
  position: absolute;
  -webkit-transform: translateZ(10px);
}

@media screen and (min-width: 768px) {
  .gear {
    width: 12px;
  }
}

.gear:nth-child(2) {
  left: calc(100% - 12px);
}

.top, .bottom {
  border-top: 2px solid #66676e;
  border-bottom: 2px solid #000;
  box-shadow: 0 6px 6px 1px rgba(0, 0, 0, .5), 0 2px 2px 1px rgba(255, 255, 255, .15);
}

.top {
  border-top-left-radius: calc(var(--radius) * .65);
  border-top-right-radius: calc(var(--radius) * .65);
  background-image: linear-gradient(#303135 0%, #38393e 100%);
  width: 100%;
  height: calc(50% - 15px);
  margin-top: 10px;
  margin-bottom: 5px;
  position: relative;
}

.bottom {
  border-bottom-left-radius: calc(var(--radius) * .65);
  border-bottom-right-radius: calc(var(--radius) * .65);
  background-image: linear-gradient(#393a3f 0%, #414147 100%);
  width: 100%;
  height: calc(50% - 15px);
  margin-top: 5px;
  margin-bottom: 10px;
  position: relative;
}

.text {
  text-align: center;
  width: 100%;
  height: 100%;
  font-size: 60px;
  line-height: 193px;
  display: block;
  position: absolute;
  overflow: hidden;
}

@media screen and (min-width: 768px) {
  .text {
    font-size: 100px;
  }
}

@media screen and (min-width: 1200px) {
  .text {
    font-size: 140px;
  }
}

.label {
  text-align: center;
  margin-top: 40px;
  font-size: 20px;
  display: block;
}

@media screen and (min-width: 768px) {
  .label {
    font-size: 40px;
  }
}

.bottom .text {
  line-height: 0;
}

.top.new, .bottom.new {
  z-index: 2;
  position: absolute;
  left: 0;
}

.top.new {
  z-index: 1;
  top: 0;
}

@media screen and (min-width: 768px) {
  .top.new:before {
    content: "";
    top: calc(var(--clockheight) / 3);
    height: calc(var(--clockheight) / 3);
    background: #000;
    width: 15px;
    position: absolute;
    left: 0;
  }

  .top.new:after {
    content: "";
    top: calc(var(--clockheight) / 3);
    height: calc(var(--clockheight) / 3);
    background: #000;
    width: 15px;
    position: absolute;
    right: 0;
  }
}

.bottom.new {
  z-index: 1;
  opacity: 0;
  height: 100%;
  position: absolute;
  top: 0;
  -webkit-transform: rotateX(.5turn);
}

.flipper.flipping .top.new {
  -webkit-transform-origin: bottom;
  -webkit-animation: rotate var(--rotationtime) 1 ease-in-out forwards;
  -webkit-transform: rotateX(-180deg)translateY(-10px);
}

.flipper.flipping .bottom.new {
  -webkit-animation: rotatebottom var(--rotationtime) 1 ease-in-out forwards;
}

@keyframes rotatebottom {
  0% {
    opacity: 0;
  }

  49% {
    opacity: 0;
  }

  50% {
    opacity: 1;
  }

  100% {
    opacity: 1;
  }
}

@keyframes rotate {
  0% {
    -webkit-transform: rotateX(0)translateY(0);
  }

  100% {
    -webkit-transform: rotateX(-180deg)translateY(-10px);
  }
}

